.footer-container {
	margin: 2em auto 0;
	padding: 1em 0;
	background-color: $color__main-superclaro;
	width: 100%!important;
	font-size: 1rem;
	z-index: 2;
}
.footer-widgets__wrap { // wraps widgets and social menu
	margin: 1em auto;
	max-width: $query__small;

	@media screen and(min-width: $query__small) {
		display: flex;
		flex-wrap: nowrap;
		justify-content: center;

		.footer-widgets {
			width: $size__ratio-large!important;
		}

		.social-menu {
			width: $size__ratio-small;
		}
	}
}

.site-footer {
	position: relative;
	padding: 1em;
	color: #fff;
	background-color: $color__main-claro;
	color: $color__main-bordes;
	text-align: center;
	font-family: $font__sans;
	font-size: .8em;

	a {
		font-weight: 400;
		color: $color__main-bordes;
		text-decoration: none;

		&:hover,
		&:focus {
			text-decoration: underline;
		}
	}

	@media screen and (min-width: $query__small) {
		padding: 1em 2em;
	}

}

.site-info {
	margin: 1em 0 1em auto;

	.info-menu {
		ul {
			list-style-type: none;
			margin: 0;
			padding: 0;

			li {
				display: inline-block;
			}
			li:not(:last-child):after {
				content: "|";
				margin: 0 .5em;
			}

		}

	}
}

.social-menu {
	margin: 1.5em 1em 0;

	p.title {
		font-family: $font__sans;
		color: $color__main-bordes;
		margin: 0;
		text-align: center;
		font-weight: 700;
		padding: 0 .5em;

		@media screen and(min-width: $query__small) {
			text-align: left;
		}
	}

	ul {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		list-style-type: none;
		margin: 0;
		padding: 0;

		@media screen and (min-width: $query__medium) {
			justify-content: flex-start;
		}

		a {
			display: block;
			padding: .2em;
			margin-right: .5em;
			color: white;
			text-decoration: none;
			color: $color__main-bordes;
			font-size: 1.6em;

			&:hover,
			&:focus {
				color: $color__main-oscuro;
			}
		}
	}
}
