.lp-single-course {


    .learn-press-breadcrumb,
    .learn-press-course-forum-link {
        font-family: $font__sans;
        font-size: .8em;
        border: none;q
        a {
            border: none;
        }
    }


}

.lp_course {
    &.has-post-thumbnail {
        .full-bleed {
            margin-top: -2em;
        }
    }
}

.widget {
    .learn-press-course-results-progress {
        background: inherit;
        border: none;
        padding: 0;
        width: 100%;
        .items-progress, .course-progress {
            padding: 0;
            margin: 0 0 1em;
        }
    }
    .avatar-cont {
        display: block;
        width: 100%;
        text-align: center;
        margin-bottom: 1em;
        .avatar {
            width: 80px;
            height: auto;
            @include borde-redondeado(50%);

        }
    }

}


.course-remaining-time {
    font-size: 80%;
    margin: 1em 0;
    .lp-label.label-enrolled, .lp-label.label-started {
        background: hsla(180, 100%, 35%, 1);
        border-radius: 0;
        padding: .4em;
        margin-right: .25em;

    }
}

.course-meta,
.price {
    font-family: $font__sans;
}

.learnpress-page .lp-button {
    line-height: 1;
    height: auto!important;
}

//programa de curso y sadiebar en lecciones
.course-curriculum
ul.curriculum-sections
.section-header
.section-title {
    font-weight: 750!important;
}

.course-curriculum ul.curriculum-sections .section-content .course-item {
    font-size: .9em!important;

}

.course-curriculum
ul.curriculum-sections
.section-header {

}


.learn-press-course-results-progress {
    display: block;
    padding: 0;
    margin-right: 0!important;

    .items-progress, .course-progress {
        padding: 1em;
        background: white;
        padding: 1em;
        margin: 0!important;
    }
    h4 {
        font-size: 1em;
        line-height: 1.2;
    }

    @media screen and (min-width: $query__small) {
        display: flex;
        justify-content: space-between;

        .items-progress, .course-progress {
            float: none!important;
        }
    }
}
.course-remaining-time {
    font-family: $font__sans;
    font-size: 80%;
    margin: 1em 0;
    .lp-label.label-enrolled, .lp-label.label-started {
        background: hsla(180, 100%, 35%, 1);
        border-radius: 0;
        padding: .4em;
        margin-right: .25em;

    }
}
